@import "variables";
@import "mixins";


.views-element-container {
  .view {
    .view-content {
      @include flexCenter;
      flex-wrap: wrap;
      gap: 20px;
      margin: 0 8%;

      .views-row {
        flex: 0 1 20%;
      }
    }

    &.view-search-news {
      #views-exposed-form-search-news-page-1 > div {
        display: flex;
        gap: $marginS;
      }

      .view-filters, .form-group {
        form {
          .form--inline {
            text-align: left !important;

            .form-item, .form-actions {
              display: flex;
              align-items: center;
            }
          }
        }
      }

      .view-content {
        .views-row {
          flex: 0 1 41%;
        }
      }
    }

    &.view-clients {
      .view-content {
        .views-row {
          flex: 0 1 45%;
        }
      }
    }
  }
}

.pager-nav {
  margin-top: 50px;
}

#views-exposed-form-search-news-page-1 > div > div.form-item.js-form-item.form-type-textfield.js-form-type-textfield.form-item-search-api-fulltext.js-form-item-search-api-fulltext.form-no-label.form-group {
  margin-left: 17%;
}

@media (max-width: 1050px) {
  .views-element-container {
    .view {
      &.view-search-news {
        .view-content {
          .views-row {
            flex: 0 1 80%;
          }
        }
      }
    }
  }
}

@media (max-width: 900px) {
  .views-element-container {
    .view {
      .view-content {
        .views-row {
          flex: 0 1 40%;
        }
      }

      &.view-clients {
        .view-content {
          .views-row {
            flex: 0 1 80%;
          }
        }
      }

      &.view-search-news {
        .view-content {
          .views-row {
            flex: 0 1 80%;
          }
        }
      }
    }
  }
}

@media (max-width: 770px) {
  .views-element-container {
    .view {
      &.view-search-news {
        .view-filters, .form-group {

          #views-exposed-form-search-news-page-1 > div {
            display: flex;
            gap: $marginS;
            justify-content: center;

            div.form-item.js-form-item.form-type-textfield.js-form-type-textfield.form-item-search-api-fulltext.js-form-item-search-api-fulltext.form-no-label.form-group {
              margin: 0;
            }
          }

        }

        .view-content {
          .views-row {
            flex: 0 1 87%;
          }
        }
      }
    }
  }
}

@media (max-width: 500px) {
  .views-element-container {
    .view {
      .view-content {
        .views-row {
          flex: 0 1 80%;
        }
      }
    }
  }
}
